import {apiUsers} from "../components/api/api";
import {Dispatch} from "redux";
import {GlobalStateType} from "./store-redux";
import {usersType} from "../components/api/apiTypes";
import {NulableType} from "../types/commonTypes";

const SET_FRIENDS = "myApp/users-reducer/SET_FRIENDS"; //  
const FRIENDS_INITIAL_STATE = "myApp/users-reducer/FRIENDS_INITIAL_STATE"; //   

type setFriendsActyionType = {type: typeof SET_FRIENDS, users: Array<usersType>}
export let setFriends = (users:Array<usersType>):setFriendsActyionType => {//    
  return {type: SET_FRIENDS, users}
};


export type friendsInitialStateActionType = {type: typeof FRIENDS_INITIAL_STATE}
export let friendsInitialState = ():friendsInitialStateActionType => {//    
  return {type: FRIENDS_INITIAL_STATE}
};

type ActionTypes = setFriendsActyionType | friendsInitialStateActionType

let initialState = {
  myFriends2: [] as NulableType<Array<usersType>>, //   
  friendsCurrentPage: 1, //    
  friendsPageSize: 50, //       
  friendsTerm: "" as string, //   
  friend: true //    ( ,  follow = true)
}
type initialStateType = typeof initialState
const sidebarReducer = (state:initialStateType = initialState, action:ActionTypes):initialStateType => {
  let stateCopy:initialStateType; //      
  switch (action.type) {
    case SET_FRIENDS:
      stateCopy = {
        ...state,
        myFriends2: action.users
      };
      return stateCopy; //   
    case FRIENDS_INITIAL_STATE:
      stateCopy = initialState;
      return stateCopy; //   
    default:
      return state;
  }
}

export let getFriendsThunkCreator = (currentPage:number, pageSize:number, term:string, friend:boolean) => {//    
  return (dispatch:Dispatch<ActionTypes>, getState: () => GlobalStateType) => { //   
    apiUsers.getUsers(currentPage, pageSize, term, friend) //       
        .then((data) => {
          dispatch(setFriends(data.items))//     
        })
  }
}

export default sidebarReducer;
