import React from 'react'; //  
import classes from './MyPosts.module.css' // css 
import Post from "./Post/Post"; //     map
import MyPostsFormik from "./Post/MyPostsFormik/MyPostsFormikBS";
import Container from "react-bootstrap/Container";
import {postsType} from "../../../types/commonTypes"; // reduxForm    

type MyPostsBSType = {
    userId: number,
    posts: Array<postsType>,
    addPost: (newPostData:string)=>void
}
const MyPostsBS:React.FC<MyPostsBSType> = ({userId, posts, addPost}) => { //    

    const postElements = posts.map((p) => //      map
        <Post key={p.id} message={p.message} like={p.like}/>);

    return (
        <div className={classes.postsBlock} /**/ >
            <Container>
                {(userId === 0) && //       (   ID  profile)
                <div>
                    <fieldset>
                        <legend>
                            <div className={classes.legendStyle}> </div>
                        </legend>
                        <MyPostsFormik
                            addPost={addPost}/> {/*        */}
                        <div className={classes.posts}>
                            {postElements} {/* */}
                        </div>
                    </fieldset>
                </div>
                }
            </Container>

        </div>
    )
}
export default MyPostsBS;
