import React from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {
    getProfileThunkCreator,
    putMyProfileThunkCreator,
    putStatusThunkCreator, setEditProfileStatus,
    setprofilePhotoThunkCreator
} from "../../redux/profile-reducer";
import {Navigate} from "react-router-dom"
import {compose} from "redux";
import NavigateToLoginHoc2 from "../hoc/NavigateToLoginHoc2";
import withRouter2 from "../hoc/withRouter2";
import {GlobalStateType} from "../../redux/store-redux";
import {ProfileType} from "../../types/commonTypes";
import {getProfileType} from "../api/apiTypes";

type ProfileContainerType = {
    profile: getProfileType, //   
    isAuth: boolean, //  ?
    myId: number, //  ID
    status: string, // 
    editProfileStatus: Array<string>  //       
    userId: number // id 
    setEditProfileStatus: (editProfileStatus: Array<string>) => void,//           
    getProfileThunkCreator: (userId: number, shouldUpdateDialogList: boolean, myId: number) => void,//      
    putStatusThunkCreator: (statusTmpInput: string, myId: number) => void,//    
    setprofilePhotoThunkCreator: (profilePhoto: any, myId: number) => void,//     
    putMyProfileThunkCreator: (MyProfile: ProfileType, myId: number) => void,

}

class ProfileContainer extends React.Component<ProfileContainerType> {
    componentDidMount() {

        this.props.getProfileThunkCreator( this.props.userId, false, 0 );//      ID
    }

    componentDidUpdate() {
        let userId = this.props.userId; //   userId  URL 

        if (userId === 0) {
            userId = this.props.myId //   ID  URL  
        }//      ( ID  URL )    
        if (userId !== this.props.profile.userId) { //    URL ID      ( )
            this.props.getProfileThunkCreator( userId, false, 0 ); //      ID
            //   setEditMode  false
        }
    }

    uploadImage = (profilePhoto: any) => {
        console.log( profilePhoto )
        this.props.setprofilePhotoThunkCreator( profilePhoto, this.props.myId )
    }

    putProfile = (putProfile: ProfileType) => { //     
        //        ID      
        let MyProfile = Object.assign( {}, {userId: this.props.myId}, putProfile );
        this.props.putMyProfileThunkCreator( MyProfile, this.props.myId )//     
    }

    render() {
        if (this.props.userId === this.props.myId) { /*      */
            return <Navigate to='../profile'/> /*    ID  URL*/
        }

        return <Profile
            profile={this.props.profile} // 
            status={this.props.status} //
            myId={this.props.myId}//  ID
            putStatusThunkCreator={this.props.putStatusThunkCreator}
            uploadImage={this.uploadImage}//  
            userId={this.props.userId}// id  (   myId    )
            // @ts-ignore
            putProfile={this.putProfile}//       
            editProfileStatus={this.props.editProfileStatus}//       
            setEditProfileStatus={this.props.setEditProfileStatus}//           
        />
    }
}

type mapStateToPropsType = {
    profile: getProfileType,
    isAuth: boolean,
    myId: number, // 
    status: string, // 
    editProfileStatus: Array<string>  //       
}

let mapStateToProps = (state: GlobalStateType) => {
    return {
        profile: state.profilePage.profile as getProfileType,
        isAuth: state.auth.isAuth as boolean,
        myId: state.auth.myId as number, // 
        status: state.profilePage.status as string, // 
        editProfileStatus: state.profilePage.editProfileStatus as Array<string>  //       
    }
}

type mapDispatchToPropsType = {
    setEditProfileStatus: (editProfileStatus: Array<string>) => void,//           
    getProfileThunkCreator: (userId: number, shouldUpdateDialogList: boolean, myId: number) => void,//      
    putStatusThunkCreator: (statusTmpInput: string, myId: number) => void,//    
    setprofilePhotoThunkCreator: (profilePhoto: any, myId: number) => void,//     
    putMyProfileThunkCreator: (MyProfile: ProfileType, myId: number) => void,
}

export default compose<any>(
    connect<mapStateToPropsType, //  mapStateToProps
        mapDispatchToPropsType, //  mapDispatchToProps
        unknown, //     
        GlobalStateType //    
        >( mapStateToProps, {
        setEditProfileStatus,//           
        getProfileThunkCreator,//      
        putStatusThunkCreator,//    
        setprofilePhotoThunkCreator,//     
        putMyProfileThunkCreator, //     myProfile
    } ),
    withRouter2,//   ID  URL     
    NavigateToLoginHoc2// ,   
)( ProfileContainer )















