import React, {useEffect, useState} from 'react';
import ButtonOverImage from '../../common/CommonClasses/ButtonOverImage.module.css'
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusUseReducer from "./ProfileStatus/ProfileStatusUseReducer";
import userPhoto1 from "../../../assets/images/no-image3.png";
import EditProfileFormikBS from "./EditProfile/EditProfileFormikBS";
import "bootstrap/dist/css/bootstrap.min.css"
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Image from 'react-bootstrap/Image'
import commonClasses from "../../common/CommonClasses/common.module.css";
import {getProfileType} from "../../api/apiTypes";
import {ProfileType} from "../../../types/commonTypes";
import ShowProfile from "./ShowProfile";



type ProfileInfoType2 = {
    profile: getProfileType,
    status: string,
    myId: number,
    userId: number,
    putProfile: ProfileType,
    putStatusThunkCreator: (statusTmpInput:string, myId:number)=>void,
    uploadImage: (profilePhoto: any)=>void,
    editProfileStatus:Array<string>,
    setEditProfileStatus: (editProfileStatus: Array<string>)=> void
}

const ProfileInfo: React.FC<ProfileInfoType2> = ({
                         profile, myId, status, putStatusThunkCreator, uploadImage,
                         userId, putProfile, editProfileStatus, setEditProfileStatus
                     }) => {

    const [profilePhoto, setprofilePhoto] = useState(userPhoto1) // useState     
    const [editMode, setEditMode] = useState(false) //    
    const [showUploadImageButton, setshowUploadImageButton] = useState(false) //      

    const editedSuccessfully = editProfileStatus.length > 0 //    / 
        && editProfileStatus[0] === "Edited successfully!" //      

    useEffect(() => {
        if (editedSuccessfully) { //      
            setEditMode(false) //    
            //       
            setTimeout(() => {
                setEditProfileStatus([]) //       
            }, 2000)
        }
    }, [editProfileStatus, editedSuccessfully, setEditProfileStatus]) //        


    if (!profile) { //     
        return <Preloader/> //  
    }
    let onChangeProfilePhoto = (e:any) => {
        setprofilePhoto(e.target.files[0]) //   useState    ( )
    }
    let displayClass = showUploadImageButton ? "" : ButtonOverImage.displayNone //  /     

    let profileStatus = <ProfileStatusUseReducer //    ProfileStatusUseState  ProfileStatusClass
        myId={myId} //  id   
        userId={profile.userId} // id  
        status={status} //   BLL
        putStatusThunkCreator={putStatusThunkCreator} //    
    />

    let showProfile = !editMode &&
        <ShowProfile profile={profile} setEditMode={setEditMode} userId={userId} myId={myId}/>

    let editProfile = editMode &&
        <div>
            <EditProfileFormikBS
                putProfile={putProfile} setEditMode={setEditMode} profile={profile}
                editProfileStatus={editProfileStatus} setEditProfileStatus={setEditProfileStatus}/>
        </div>
    let editMyPhoto = (userId === 0) &&//       (   ID  profile)
        <div>
            <form> {/*     */}
                <span><button
                    className={ButtonOverImage.btn1 + " " + displayClass} //   -    +  /  
                    onMouseOver={() => {
                        setshowUploadImageButton(true)
                    }} //      setshowUploadImageButton  true ( )
                    onMouseOut={() => {
                        setshowUploadImageButton(false)
                    }}//       setshowUploadImageButton  false ( )
                    onClick={() => { //
                        uploadImage(profilePhoto)
                    }}></button></span> {/*     */}
                <span>
                    <input
                        className={ButtonOverImage.btn2 + " " + displayClass} //   -    +  /  
                        onMouseOver={() => {
                            setshowUploadImageButton(true)
                        }} //      setshowUploadImageButton  true ( )
                        onMouseOut={() => {
                            setshowUploadImageButton(false)
                        }}//       setshowUploadImageButton  false ( )
                        type="file" onChange={onChangeProfilePhoto}/></span> {/* */}
            </form>
        </div>

    let showUserPhoto = <Image fluid={true}
        alt={"userPhoto"}
        onMouseOver={() => {
            setshowUploadImageButton(true)
        }} //  
        onMouseOut={() => {
            setshowUploadImageButton(false)
        }}
        className={`${ButtonOverImage.profilePhotoIMG} ${userId === 0 && showUploadImageButton ? ButtonOverImage.ImgHover : ""}`}
        //     (userId === 0)     ,  ImgHover  (  :hover)
        src={profile.photos.large ? profile.photos.large : userPhoto1}/>

    return <div>
        <Container fluid="sm">
            <h2 className={commonClasses.pageHeader}>Profile</h2> {/**/}

            <Row >
                <Col xs={12} md={5} className={ButtonOverImage.container}>
                    {showUserPhoto} {/*  */}
                    {editMyPhoto} {/*  ,    */}
                </Col>
                <Col xs={12} md={7}>
                    {showProfile} {/* */}
                    {editProfile} {/* */}

                    {/*{editProfileStatus} {/*   (/)*/}
                    {/*   ,   .
                  sucessully,    ,          
                  ,       */}
                    <div>
                        {editedSuccessfully //      
                            && <div>
                                {editProfileStatus[0]} {/*    */}
                            </div>
                        }
                    </div>
                    <div>
                        {profileStatus} {/*  */}
                    </div>
                </Col>
            </Row>
        </Container>
    </div>
}
export default ProfileInfo;
