import {apiProfile} from "../components/api/api";
import {getAuthMeThunkCreator, setMyProfile, setMyProfileActionType} from "./auth-reducer";
import {updateDialogListThunkCreator} from "./dialogs-reducer";
import {NulableType, postsType, ProfileType} from "../types/commonTypes";
import {Dispatch} from "redux";
import {ThunkAction} from "redux-thunk";
import {GlobalStateType} from "./store-redux";
import {getProfileType} from "../components/api/apiTypes";
import {ResultCodeEnum} from "../components/api/enum";

const SET_EDIT_PROFILE_ERROR= "myApp/auth-reducer/SET_EDIT_PROFILE_ERROR"; //    

type setEditProfileStatusActionType ={type: typeof SET_EDIT_PROFILE_ERROR, editProfileStatus: Array<string>}
export let setEditProfileStatus = (editProfileStatus: Array<string>):setEditProfileStatusActionType => { //           
    return {type: SET_EDIT_PROFILE_ERROR, editProfileStatus}
};

const DELETE_POST = "myApp/profile-reducer/DELETE_POST";//    

type deletePostActionCreatorActionType = {type:typeof DELETE_POST, postId:number}
export let deletePostActionCreator = (postId:number):deletePostActionCreatorActionType => { //     postId
    return {type: DELETE_POST, postId}
};

const ADD_POST = "myApp/profile-reducer/ADD-POST";//    

type addPostActionCreatorActionType ={type:typeof ADD_POST, newPostData: string}
export let addPostActionCreator = (newPostData: string):addPostActionCreatorActionType => { //   
    return {type: ADD_POST, newPostData}
};

const SET_USER_PROFILE = "myApp/profile-reducer/SET_USER_PROFILE"; //        

type setUserProfileActionType ={type:typeof SET_USER_PROFILE, profile: getProfileType}
    let setUserProfile = (profile: getProfileType):setUserProfileActionType => { //        
    return {type: SET_USER_PROFILE, profile}
};

const SET_STATUS = "myApp/profile-reducer/SET_STATUS" //    

type setStatusActionType ={type:typeof SET_STATUS, newStatus: string}
export let setStatus = (newStatus: string):setStatusActionType => { //    ( API )
    return {type: SET_STATUS, newStatus}
};

const PROFILE_INITIAL_STATE = "myApp/profile-reducer/PROFILE_INITIAL_STATE" //    

export type profileInitialStateActionType ={type:typeof PROFILE_INITIAL_STATE}
export let profileInitialState = ():profileInitialStateActionType => { //   
    return {type: PROFILE_INITIAL_STATE}
};

const SET_PROFILE_PHOTO = "myApp/profile-reducer/SET_PROFILE_PHOTO" //    

type setProfilePhotoActionType = {type:typeof SET_PROFILE_PHOTO}
export let setProfilePhoto = ():setProfilePhotoActionType => { //   
    return {type: SET_PROFILE_PHOTO}
};

type ActionTypes = setProfilePhotoActionType | profileInitialStateActionType | setStatusActionType | setUserProfileActionType |
    addPostActionCreatorActionType | deletePostActionCreatorActionType | setEditProfileStatusActionType | setMyProfileActionType

let initialState = {
    posts: [//     
        {id: 1, message: "state 2 Hi, how are you?", like: 12},
        {id: 2, message: "state 2 it's, my first post", like: 15},
    ] as Array<postsType>,
    profile: null as NulableType<getProfileType> , //      
    status: "", //      
    editProfileStatus: [] as Array<string>, //       
}

type initialStateType = typeof initialState

export let profileReducer = (state:initialStateType = initialState, action:ActionTypes):initialStateType => { //  
    let stateCopy:initialStateType; //      
    switch (action.type) {
        case SET_USER_PROFILE: //       
            stateCopy = {
                ...state,
                profile: action.profile //     
            }
            return stateCopy;
        case ADD_POST: {//  
            const newPost:postsType  = { //     
                id: 5, // id    ()
                message: action.newPostData, //     
                like: 2 //   ()
            };
            stateCopy = {
                ...state,
                posts: [...state.posts, newPost], //      
            }
            return stateCopy;

        }
        case DELETE_POST: {//    postId
            stateCopy = {
                ...state,
                posts: [...state.posts.filter(f=>f.id!==action.postId)], //     
            }
            return stateCopy;
        }
        case SET_STATUS: //      ( API )
            stateCopy = {
                ...state,
                status: action.newStatus
            }
            return stateCopy;
        case PROFILE_INITIAL_STATE: //  
            stateCopy = initialState
            return stateCopy;
        case SET_EDIT_PROFILE_ERROR: //          
            stateCopy = {
                ...state,
                editProfileStatus: action.editProfileStatus
            }
            return stateCopy; //     
        default:
            return state;
    }
}

type ThunkType = ThunkAction<
    void,    //    
    GlobalStateType,    //    redux
    unknown,    //   
    ActionTypes //   ActionCreator
    >


export let getProfileThunkCreator = (userId:number, shouldUpdateDialogList:boolean, myId:number):ThunkType => { //      
    return async (dispatch, getState) => { //       
        let CommonPart = (response: getProfileType, userId:number) => { //         
            dispatch(setUserProfile(response)) //     
            dispatch(getStatusThunkCreator(userId)) //   
            if (shouldUpdateDialogList) {//     
                dispatch(updateDialogListThunkCreator(myId, response.userId, response.fullName, response.photos.small  )) //  
            }
        }

        if (!userId) { //  userId    URL (       ID  )
            const response = await apiProfile.getAuthMe() //    " ?"    ID ( )
            if (response.resultCode === ResultCodeEnum.Success) { //        ID
                userId = response.data.id; //   ID  userId
                const response2 = await apiProfile.getProfile(userId) //      
                CommonPart(response2, userId)  //         
            }
        } else { //  userId   URL
            const response = await apiProfile.getProfile(userId) //
            CommonPart(response, userId) //         
        }
    }
}

export let getStatusThunkCreator = (userId:number):ThunkType => {  //     
    return async (dispatch, getState) => { //      
        const response = await apiProfile.getStatus(userId) // api     userId
        dispatch(setStatus(response)) //        
    }
}
export let putStatusThunkCreator = (statusTmpInput:string, myId:number):ThunkType => { //    
    return async (dispatch, getState) => { //     
        const response = await apiProfile.putStatus(statusTmpInput) //     
        if (response.resultCode === ResultCodeEnum.Success) { //      
            dispatch(getStatusThunkCreator(myId))//       
        }
    }
}
export let setprofilePhotoThunkCreator = (profilePhoto:any, myId:number):ThunkType => { //     
    return async (dispatch, getState) => { //      
        const response = await apiProfile.putPhoto(profilePhoto) //     
        if (response.resultCode === ResultCodeEnum.Success) { //      
            dispatch(getProfileThunkCreator(myId,false, 0));//      
            dispatch(getAuthMeThunkCreator()) //     (header photo)   
        }
    }
}

export let putMyProfileThunkCreator = (MyProfile:ProfileType, myId:number):ThunkType => { //     myProfile
    return async (dispatch, getState) => { //      myProfile
        const response = await apiProfile.putMyProfileData(MyProfile) //     
        if (response.resultCode === ResultCodeEnum.Success) { //      
            const response2 = await apiProfile.getProfile(myId)//       
            dispatch(setMyProfile(response2))//     
            dispatch(getProfileThunkCreator(myId, false, 0))
            dispatch(setEditProfileStatus(["Edited successfully!"])) //     
        } else { //         
            let message =  //    message -   
                response.messages && response.messages.length!==0  //  response.messages       0
                    ? response.messages //     
                    : ["no responce from server"] //    
            dispatch(setEditProfileStatus(message)) //     
        }
    }
}


export default profileReducer;










