import React from "react";
import './theme.scss';
import commonClasses from "./components/common/CommonClasses/common.module.css";
import {HashRouter} from "react-router-dom";
import HeaderContainer from "./components/Header/HeaderContainer";
import {connect} from "react-redux";
import {initialisedAppThunkCreator} from "./redux/app-reducer";
import Preloader from "./components/common/Preloader/Preloader";
import ContentContainer from "./components/Content/ContentContainer";
import ErrorBoundary from "./components/common/ErrorBoundary/ErrorBoundary";
import Container from "react-bootstrap/Container";
import FooterBS from "./components/Footer/FooterBS";

class App extends React.Component { //  app      
    componentDidMount() {
        this.props.initialisedAppThunkCreator() //   
    }

    render() {

        if (!this.props.initialisedApp) { //     
            return <Preloader/> //   
        }
        return ( //    
            <HashRouter> {/*BrowserRouter  , HashRouter  gh-pages*/}
                <div className={`${"themeCommon"} ${this.props.theme === "light"?"light":"dark"}`}>
                    {/*    */}
                    <ErrorBoundary> {/*     */}
                        <Container className={commonClasses.minwidth}>
                            <HeaderContainer/>  {/* */}
                            <ContentContainer/> {/*     URL*/}
                            <FooterBS/>
                        </Container>
                    </ErrorBoundary>
                    {/*      header,  div   350*/}
                </div>

            </HashRouter>

        );
    }
}

const mapStateToProps = (state) => {
    return {
        initialisedApp: state.app.initialisedApp, //   
        theme: state.theme.themeBLL, //      
    }
}

export default connect(mapStateToProps, {initialisedAppThunkCreator})(App);
