import React, {ChangeEvent, SyntheticEvent, useReducer} from "react";
import "bootstrap/dist/css/bootstrap.min.css"
import InputGroup from 'react-bootstrap/InputGroup';
import Form from 'react-bootstrap/Form';


type ProfileStatusUseReducerType = {
    myId:number, //  id   
    userId: number, // id  
    status:string, //   BLL
    putStatusThunkCreator: (statusTmpInput:string, myId:number)=>void, //    
}
let ProfileStatusUseReducer:React.FC<ProfileStatusUseReducerType> = ({status, userId, myId, putStatusThunkCreator}) => {
    const initialState = {
        modifyStatus2: false,//  -  
        statusTmpInput2: "" //       ( input)
    }
    type initialStateType = typeof initialState

    const SET_MODIFY_STATUS_TRUE = "SET_MODIFY_STATUS_TRUE"; //  (modify true)
    const SET_MODIFY_STATUS_FALSE = "SET_MODIFY_STATUS_FALSE";//  (modify false)
    const SET_STATUS_TMP_INPUT = "SET_STATUS_TMP_INPUT";//  (  input)

    type setModifyStatusTrue = {type:typeof SET_MODIFY_STATUS_TRUE}
    type setModifyStatusFalse = {type:typeof SET_MODIFY_STATUS_FALSE}
    type setStatusTmpInput = {type:typeof SET_STATUS_TMP_INPUT, text: string}

    type ActionTypes = setModifyStatusTrue | setModifyStatusFalse | setStatusTmpInput

    const localReducer = (localState:initialStateType, action:ActionTypes):initialStateType => {
        let stateCopy; //    
        switch (action.type) {
            case SET_MODIFY_STATUS_TRUE: //     input ( )
                stateCopy = {
                    ...localState,
                    modifyStatus2: true, //    true
                    statusTmpInput2: status//    input   
                };
                return stateCopy; //      
            case SET_MODIFY_STATUS_FALSE: //     
                stateCopy = {
                    ...localState,
                    modifyStatus2: false //    false
                };
                return stateCopy; //      
            case 'SET_STATUS_TMP_INPUT':
                stateCopy = {
                    ...localState,
                    statusTmpInput2: action.text //     value input
                };
                return stateCopy; //      
            default:
                throw new Error(); //   
        }
    }

    const [localState, dispatch] = useReducer(localReducer, initialState)//   useState  useReducer

    type checkIfICanModifyStatusType = () => void
    const checkIfICanModifyStatus = () => {// ,      (    )
        if (userId === myId) { //  ID    
            dispatch({type: SET_MODIFY_STATUS_TRUE})//       input
        }
    }
    const setMyStatus = () => { //       input    Enter
        dispatch({type: SET_MODIFY_STATUS_FALSE})//       input

        putStatusThunkCreator(localState.statusTmpInput2, myId)//      
    }
    const onChangeStatus = (event:ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const text = event.target.value;//       input
        dispatch({type: SET_STATUS_TMP_INPUT, text: text})//           
    }
    const checkEnterPressed = (e: React.KeyboardEvent) => { //   Enter
        if (e.charCode === 13) {
            setMyStatus()//    Enter
        }
    }

    type CommonInputGroupType = {
        isDisabled: boolean,
        onClickMethod: checkIfICanModifyStatusType,
        value:string
    }
    const CommonInputGroup:React.FC<CommonInputGroupType>  = ({isDisabled, onClickMethod, value}) => {
        if (!value) {
            value="";
        }
        //   ()    
        return <InputGroup className="my-3" onClick={onClickMethod}> {/*    */}
            <InputGroup.Text id="basic-addon1">:</InputGroup.Text> {/*    */}
            <Form.Control
                onClick={onClickMethod}
                value={value} //            
                onChange={(e)=>onChangeStatus(e)} //    
                onBlur={setMyStatus}//      input
                autoFocus //     
                placeholder={" "}//     
                onKeyPress={(e)=>checkEnterPressed(e)} //   Enter
                disabled={isDisabled}
            />
        </InputGroup>
    }
    return (<div>
        {!localState.modifyStatus2 //    
            ? <CommonInputGroup isDisabled={true} onClickMethod={checkIfICanModifyStatus} value={status}/>
            : <CommonInputGroup isDisabled={false} onClickMethod={setMyStatus} value={localState.statusTmpInput2}/>
        }
    </div>)
}
export default ProfileStatusUseReducer







